import React, { useEffect, useRef } from 'react';
import { connect } from 'react-redux';
//import { SetTimeOutAnswer } from './../actions/quiz';
import { makeStyles } from '@material-ui/core/styles';
import './Quiz.css';

import { playTimeoverSound } from './util/SoundPlay'

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'absolute',
        width: "100%",
        height: "100%",
    },
}));

var _enableTimeoverSignal = false;
export const QuizSubmitButton = ({ finishTime, onClick }) => {
    const classes = useStyles();
    const progress = useRef(null);
    const [_leftTime, setLeftTime] = React.useState(0);
    const [_maxDuration, setMaxDuration] = React.useState(0);

    useEffect(() => {
        var currentTime = new Date().getTime();
        //console.log('start:' + currentTime);
        //console.log('finish:' + finishTime);
        setMaxDuration((finishTime - currentTime)/1000);
        //console.log('_maxDuration:' + (finishTime - currentTime));
        var intervalID = setInterval(() => {
            currentTime = new Date().getTime();
            if (currentTime > finishTime) {
                currentTime = finishTime;
            }
            var leftTime = (finishTime - currentTime)/1000;
            //console.log('leftTime:' + leftTime);
            setLeftTime(leftTime);
            if (currentTime >= finishTime && finishTime > 0) {
                console.log('setInterval Finish');
                clearInterval(intervalID);
                intervalID = undefined;
                onClick();
            }
        }, 100);

        return () => {
            console.log('setInterval clear call');
            if (undefined != intervalID) {
                console.log('setInterval clear');
                clearInterval(intervalID);
            }
        }
    }, [finishTime]);

    useEffect(() => {
        if (_maxDuration > 0) {
            var leftTimePercent = parseInt(_leftTime*100/_maxDuration);
            //console.log('left percent:' + leftTimePercent);
            if (progress.current) {
                progress.current.style.width = leftTimePercent + '%';
            }
        }
    }, [_leftTime]);

    const getLeftTime = () => {
        var time_number;
        if (parseInt(_leftTime) < 3) {
            time_number = parseFloat(_leftTime).toFixed(1);
            if (true == _enableTimeoverSignal) {
                _enableTimeoverSignal = false;
                playTimeoverSound();
            }
        }
        else {
            time_number = parseInt(_leftTime);
            _enableTimeoverSignal = true;
        }
        return (<div>{time_number}초 남음</div>)
    }

    return (
        <div className={classes.root}>
            <div className='quizSubmit_box' onClick={onClick}>
                <div className='quizSubmit_progress progressbar_value_blend' ref={progress}>
                </div>
                <div className='quizSubmit_text'>
                    <div className='quizSubmit_title'>제출하기</div>
                    <div className='quizSubmit_leftValue'>{getLeftTime()}</div>
                </div>
            </div>
        </div>
    );
}

export default QuizSubmitButton;
