import { SETSCORE, SETCHECKANSWER, SETQUIZSTEP, SETQUIZRESET } from '../actions/quiz'

const initialState = {
    _score: 0,
    _checkAnswer: false,
    _step: 0, 
    // _timeOutAnswer: false
};

export default function quiz(state = initialState, action) {
    console.log('reducer scene action call:' + action.type);
    switch(action.type) {
        case SETSCORE:
            console.log('SETSCORE:' + action._score);
            return Object.assign({}, state, {
                _score: action._score
            });
        case SETCHECKANSWER:
            console.log('SETCHECKANSWER:' + action._checkAnswer);
            return Object.assign({}, state, {
                _checkAnswer: action._checkAnswer
            });
        case SETQUIZSTEP:
            console.log('SETQUIZSTEP:' + action._step);
            return Object.assign({}, state, {
                _step: action._step
            });
        case SETQUIZRESET:
            console.log('SETQUIZRESET');
            return Object.assign({}, state, {
                _step: 0,
                _score: 0,
                _checkAnswer: false
            });
        default:
            return state;
    }
}