import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
    root: {
        position: "fixed",
        backgroundColor: '#FFFFFF88',
        width: '100%',
        height: '65px',
        bottom: '0px',
        left: '0%'
    },
    BottomBarButton: {
        position: "absolute",
        height: "80%",
        top: "8%",
        background: 'linear-gradient(90deg, #cc6699 30%, #6666ff 80%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        boxShadow: '0 3px 5px 2px rgba(0, 0, 0, .3)',
        width: "94%",
        left: "3%",
    },
    BottomBarText: {
        position: "absolute",
        height: "60%",
        top: "0%",
        color: 'white',
        width: "100%",
        left: "0%",
        fontSize: "20px",
        textAlign: "center"
    },
    BottomBarSubGroup: {
        position: "absolute",
        height: "40%",
        top: "50%",
        width: "100%",
        left: "0%",
    },
    BottomBarImg : {
        position: "relative",
        //left: "0%",
        top : "3px",
        height: "80%",
        width: "auto"
    },
    BottomBarSubText: {
        position: "relative",
        height: "100%",
        //top: "0%",
        color: 'white',
        width: "100%",
        left: "5px",
        fontSize: "15px",
        textAlign: "center"
    },
}));

export const RewardButton = ({ onClick, rewardCategory, rewardExp }) => {
    const classes = useStyles(); 

    const getRewardIconPath = () => {
        //console.log('=====rewardCategory:'+ rewardCategory);
        switch (rewardCategory) {
            case 'strength': return 'https://goli8111.github.io/level-up/img/class_strength.png';
            case 'dexterity': return 'https://goli8111.github.io/level-up/img/DexterityStat_icon.png';
            case 'intelligence': return 'https://goli8111.github.io/level-up/img/class_intelligence.png';
            case 'wisdom': return 'https://goli8111.github.io/level-up/img/class_wisdom.png';
        }
    }

    return (
        <div className={classes.root}>
            <Button onClick={onClick} className={classes.BottomBarButton} variant="contained" color="secondary">
                <span className={classes.BottomBarText}>완료하고 보상 받기</span>
                <div className={classes.BottomBarSubGroup}>
                    <img className={classes.BottomBarImg} src={getRewardIconPath()}></img>
                    <span className={classes.BottomBarSubText} >{rewardExp}</span>
                </div>
            </Button>
        </div>
    );
}

export default RewardButton;
