

export function CreateQuizData(quizFullData) {
    var quizData = [];
    var quiz_random_Number = [];
    console.log()
    var randoms = randomNumbers(quizFullData.Data.length),
        rand = randoms(),
        quiz_random_Number = [];

    while (rand != null) {
        quiz_random_Number.push(rand);
        rand = randoms();
    }

    var count = quizFullData.Config["quiz_count"];
    if (count > quizFullData.Data.length) {
        count = quizFullData.Data.length;
    }

    for (var i = 0; i < count; i++) {
        var quizIndex = quiz_random_Number[i];
        quizData.push(quizFullData.Data[quizIndex]);
    }
    return quizData;
}

function randomNumbers(max) {
    function range(upTo) {
        var result = [];
        for(var i = 0; i < upTo; i++) result.push(i);
        return result;
    }
    function shuffle(o){
        for(var j, x, i = o.length; i; j = Math.floor(Math.random() * i), x = o[--i], o[i] = o[j], o[j] = x);
        return o;
    }
    var myArr = shuffle(range(max));
    return function() {
        return myArr.shift();
    };
}