import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import QuizScene from './QuizScene';
import { CreateQuizData } from '../components/util/CreateQuizRandomData';

const useStyles = makeStyles((theme) => ({
    root: {
        position: "absolute",
        width: "100%",
        height: "100%",
        backgroundColor: "black"
    }
}));

export const QuizSceneRoot = ({ quizData }) => {
    console.log('QuizSceneRoot component start');
    const classes = useStyles();
    const [_quizData, setQuizData] = React.useState(undefined);

    useEffect(() => {
        updateQuizData();
    }, []);

    const updateQuizData = () => {
        var data = CreateQuizData(quizData);
        setQuizData(data);
        console.log(data);
    }

    return (
        <div className={classes.root}>
            <QuizScene quizConfig={quizData.Config} quizData={_quizData} />
        </div>
    );
}

export default QuizSceneRoot;