import React from 'react';
import './Quiz.css';

export const QuizBodyTitle = ({ step, quizData }) => {
    var right_answer_score = 0;
    if (undefined != quizData) {
        right_answer_score = quizData.right_answer_score;
    }
    var wrong_answer_score = 0;
    if (undefined != quizData) {
        wrong_answer_score = quizData.wrong_answer_score;
    }

    const getWrongScore = () => {
        if (wrong_answer_score != 0) {
            return ' -' + wrong_answer_score;
        }
        return ' ' + wrong_answer_score;
    }

    return (
        <div >
            <div className='quizBody_title_question_index'>#{step + 1}</div>
            <div className='quizScore_title_question_score'>
                <img className='quizScore_title_question_right_icon' src={process.env.PUBLIC_URL + '/img/answer_icon.png'} /> +{right_answer_score}
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        <img className='quizScore_title_question_right_icon' src={process.env.PUBLIC_URL + '/img/wrong_icon.png'} /> 
                        {getWrongScore()}
            </div>
        </div>
    );
}

export default QuizBodyTitle;
