import React from 'react';
import './App.css';
import IndexScene from './scene/IndexScene'
import { Route, BrowserRouter } from 'react-router-dom';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Route path="/" component={IndexScene}/>
      </BrowserRouter>
    </div>
  );
}

export default App;
