import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        position: "fixed",
        top: "0%",
        width: "100%",
        height: "100%",
        backgroundColor: '#00000088',
    },
    text: {
        position: "fixed",
        width: '100%',
        height: '100%',
        textAlign: 'center',
        top: '40%',
        fontSize: '400%',
        color: 'white',
        fontWeight: 'bold',
        textShadow: '2px 2px 5px black',
    }
}));

export const TextCountdown = ({ count }) => {
    const classes = useStyles();

    const getText = () => {
        switch (count) {
            case 1:
                return (<span>READY</span>);
            case 2:
                return (<span>SET</span>);
            default:
                return (<span>GO</span>);
        }
    }

    // 카운팅 값이 없으면 렌더링 하지 않음
    if (count == 0) {
        return <div></div>;
    } else {
        return (
            <div className={classes.root}>
                <div className={classes.text}>
                    {getText()}
                </div>
            </div>
        );
    }
}

export default TextCountdown;
