export const SETSCORE = 'SETSCORE';
export function SetScore(value) {
    return {
        type: SETSCORE,
        _score: value
    };
}

export const SETCHECKANSWER = 'SETCHECKANSWER';
export function SetCheckAnswer(value) {
    return {
        type: SETCHECKANSWER,
        _checkAnswer: value
    };
}

export const SETQUIZRESET = 'SETQUIZRESET';
export function SetQuizReset() {
    return {
        type: SETQUIZRESET
    };
}

export const SETQUIZSTEP = 'SETQUIZSTEP';
export function SetQuizStep(value) {
    return {
        type: SETQUIZSTEP,
        _step: value
    };
}
