import React, {useEffect, useRef} from 'react';
import { connect } from 'react-redux';
import { SetCheckAnswer, SetScore, SetQuizStep } from '../actions/quiz';
import { SetScene } from '../actions/scene'
import QuizMessageBox from './QuizMessageBox';
import './Quiz.css';
import { playEnableClickSound, playQuizCorrectAnswerSound, playQuizWrongAnswerSound} from './util/SoundPlay'

var _selectedCheckboxes = new Set();
export const QuizBodyQuestion = ({ quizData, _checkAnswer, SetCheckAnswer, SetScene, SetScore, _score, fullQuizData, SetQuizStep, _step, finishTime }) => {

    useEffect(() => {
        if (true == _checkAnswer) {
            checkAnswer(quizData);
        }
    }, [_checkAnswer]);

    useEffect(() => {
        ResetInputValue();
    }, [_step]);

    const [_radioSelect, setRadioSelect] = React.useState(0);
    const [_checkSelect1, setCheckSelect1] = React.useState(false);
    const [_checkSelect2, setCheckSelect2] = React.useState(false);
    const [_checkSelect3, setCheckSelect3] = React.useState(false);
    const [_checkSelect4, setCheckSelect4] = React.useState(false);
    const [_checkSelect5, setCheckSelect5] = React.useState(false);
    const [_checkSelect6, setCheckSelect6] = React.useState(false);
    const [_textInput, setTextInput] = React.useState('');
    const [_scoreValue, setScoreValue] = React.useState(0);
    const [_showMessageBox, setShowMessageBox] = React.useState('');

    var questionText = '';
    if (undefined != quizData) {
        questionText = quizData.question;
    }
    var subQuestionText = undefined;
    if (undefined != quizData) {
        subQuestionText = quizData.sub_question_1;
    }

    const onNextStep = () => {
        console.log('onNextStep call:' + _step);
        var nextStep = _step + 1;
        if (fullQuizData.length > nextStep) {
            SetQuizStep(nextStep);
        } else {
            SetScene('ResultScene')
        }
    }

    const ResetInputValue = () => {
        setRadioSelect(0);
        setCheckSelect1(false);
        setCheckSelect2(false);
        setCheckSelect3(false);
        setCheckSelect4(false);
        setCheckSelect5(false);
        setCheckSelect6(false);
        setTextInput('');
        setShowMessageBox('');
        SetCheckAnswer(false);
    }

    const onMessageBox = (type, duration) => {
        setShowMessageBox(type);
        setTimeout(() => {
            setShowMessageBox('');
        }, duration);
    }

    const checkAnswer = () => {
        if (undefined == quizData) {
            return;
        }
        var rightAnswer = quizData.right_answer;
        var userAnswer = '';
        switch (quizData.form_type) {
            case 'checkbox': {
                var answer = '';
                if (true == _checkSelect1) { if (answer.length>0) {answer += ','; } answer += '1'; }
                if (true == _checkSelect2) { if (answer.length>0) {answer += ','; } answer += '2'; }
                if (true == _checkSelect3) { if (answer.length>0) {answer += ','; } answer += '3'; }
                if (true == _checkSelect4) { if (answer.length>0) {answer += ','; } answer += '4'; }
                if (true == _checkSelect5) { if (answer.length>0) {answer += ','; } answer += '5'; }
                if (true == _checkSelect6) { if (answer.length>0) {answer += ','; } answer += '6'; }
                userAnswer = answer;
                console.log('checkbox select:' + answer);
            } break;
            case 'radio':{
                if (0 != _radioSelect) {
                    userAnswer = _radioSelect;
                    console.log('radio select:' + _radioSelect);
                }
            } break;
            case 'text': {
                userAnswer = _textInput;
                var answertList = rightAnswer.split(",");
                answertList.forEach(function (answer) {
                    console.log(answer);
                    if (answer == userAnswer) {
                        rightAnswer = answer;
                    }
                });
                console.log('text input:' + _textInput)
            } break;
        }

        var currentTime = new Date().getTime();
        console.log('finishTime:'+finishTime + ', current:' + currentTime);
        if (finishTime > currentTime) {
            if (userAnswer.length == 0) {
                console.log('정답을 입력해야 합니다.');
                SetCheckAnswer(false);
                onMessageBox('none', 1000);
                return;
            }
        }

        var targetScore = 0;
        if (rightAnswer == userAnswer) {
            console.log('정답');
            targetScore = parseInt(quizData.right_answer_score);
            onMessageBox('good', 2000);
            playQuizCorrectAnswerSound();
        } else {
            console.log('오답');
            targetScore = -parseInt(quizData.wrong_answer_score);
            onMessageBox('bad', 2000);
            playQuizWrongAnswerSound();
        }
        SetScore(_score + targetScore);
        setScoreValue(targetScore);

        setTimeout(() => {
            SetCheckAnswer(false);
            onNextStep();
        }, 2000);
    }
    const onRadioSelectChange = (e) => {
        console.log('onRadioSelectChange call:' + e.target.value);
        setRadioSelect(e.target.value);
        playEnableClickSound();
    }

    const createRadioElement = (index, desc) => {
        var id = 'radio-' + index;
        return (
            <div key={desc}>
                <input id={id} className="radio-custom" name="radio-group" type="radio" checked={_radioSelect == index} onChange={onRadioSelectChange} value={index} />
                <label htmlFor={id} className="radio-custom-label">{desc}</label>
            </div>
        );
    }
    const createRadioFrom = () => {
        if (quizData == undefined) {
            return undefined;
        }
        let contents = [];
        if (undefined != quizData.case_1) {contents.push(createRadioElement(1, quizData.case_1));}
        if (undefined != quizData.case_2) {contents.push(createRadioElement(2, quizData.case_2));}
        if (undefined != quizData.case_3) {contents.push(createRadioElement(3, quizData.case_3));}
        if (undefined != quizData.case_4) {contents.push(createRadioElement(4, quizData.case_4));}
        if (undefined != quizData.case_5) {contents.push(createRadioElement(5, quizData.case_5));}
        if (undefined != quizData.case_6) {contents.push(createRadioElement(6, quizData.case_6));}
        return contents;
    }

    const createCheckboxElement = (index, checkboxState, desc) => {
        var id = 'checkbox-' + index;
        console.log(id + ' state : ' + checkboxState);
        return (
            <div key={desc}>
                <input id={id} className="checkbox-custom" name={id} type="checkbox" checked={checkboxState} onChange={onCheckboxSelectChange} value={index} />
                <label htmlFor={id} className="checkbox-custom-label">{desc}</label>
            </div>
        );
    }
    const onCheckboxSelectChange = (e) => {
        console.log('onCheckSelectChange call:' + e.target.value);
        console.log('onCheckSelectChange checked:' + e.target.checked);
        if (e.target.value == 1) {
            setCheckSelect1(e.target.checked);
        } else if (e.target.value == 2) {
            setCheckSelect2(e.target.checked);
        } else if (e.target.value == 3) {
            setCheckSelect3(e.target.checked);
        } else if (e.target.value == 4) {
            setCheckSelect4(e.target.checked);
        } else if (e.target.value == 5) {
            setCheckSelect5(e.target.checked);
        } else if (e.target.value == 6) {
            setCheckSelect6(e.target.checked);
        }
        playEnableClickSound();
    }

    const onTextInputChange = (e) => {
        console.log('onTextInputChange call:' + e.target.value);
        setTextInput(e.target.value);
    }

    const createCheckboxFrom = () => {
        if (quizData == undefined) {
            return undefined;
        }
        let contents = [];
        if (undefined != quizData.case_1) {contents.push(createCheckboxElement(1, _checkSelect1, quizData.case_1));}
        if (undefined != quizData.case_2) {contents.push(createCheckboxElement(2, _checkSelect2, quizData.case_2));}
        if (undefined != quizData.case_3) {contents.push(createCheckboxElement(3, _checkSelect3, quizData.case_3));}
        if (undefined != quizData.case_4) {contents.push(createCheckboxElement(4, _checkSelect4, quizData.case_4));}
        if (undefined != quizData.case_5) {contents.push(createCheckboxElement(5, _checkSelect5, quizData.case_5));}
        if (undefined != quizData.case_6) {contents.push(createCheckboxElement(6, _checkSelect6, quizData.case_6));}
        return contents;
    }

    const createTextFrom = () => {
        if (quizData == undefined) {
            return undefined;
        }
        return (
            <div>
                <input type="text" name="text-choice" className="text-input-custom" onChange={onTextInputChange} />
            </div>
        );
    }

    const getAnswerForm = () => {
        if (quizData == undefined) {
            return undefined;
        }

        switch (quizData.form_type) {
            case 'checkbox':
                return createCheckboxFrom();
            case 'radio':
                return createRadioFrom();
            case 'text':
                return createTextFrom();
        }
    }

    return (
        <div>
            <div className='quizBodyQuestion_group'>
                <div className='quizBodyQuestion_question'>{questionText}</div>
                {subQuestionText ? <div className='quizBodyQuestion_subquestion'>{subQuestionText}</div> : undefined}
            </div>
            <div className='quizBodyQuestion_answer_group'>
                <div className='quizBodyQuestion_answer'>
                    {getAnswerForm()}
                </div>
            </div>
            {_showMessageBox.length > 0 ? <QuizMessageBox type={_showMessageBox} score={_scoreValue} /> : undefined}
        </div> 
    );
}

const mapStateToProps = (state) => ({
    _checkAnswer: state.quiz._checkAnswer,
    _score: state.quiz._score,
    _step: state.quiz._step
});
const mapDispatchToProps = { SetScene, SetCheckAnswer, SetScore, SetQuizStep };

export default connect(mapStateToProps, mapDispatchToProps)(QuizBodyQuestion);