import React from 'react';
import './Quiz.css';
import QuizBodyTitle from './QuizBodyTitle';
import QuizBodyQuestion from './QuizBodyQuestion';

export const QuizBody = ({ step, fullQuizData, quizData, finishTime }) => {
    return (
        <div className='quizBody_box'>
            <div className='quizBody_title'>
                <QuizBodyTitle step={step} quizData={quizData} />
            </div>
            <div className='quizBodyQuestion'>
                <QuizBodyQuestion fullQuizData={fullQuizData} quizData={quizData} finishTime={finishTime} />
            </div>
        </div>
    );
}

export default QuizBody;
