import React, {useEffect} from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

export const QuizStartConfirmDialog = ({onStartClick, onCloseClick, openDialog}) => {
  return (
    <div>
      <Dialog
        open={openDialog}
        onClose={onCloseClick}
        aria-labelledby="alert-dialog-title"
      >
        <DialogTitle id="alert-dialog-title" style={{textAlign:'center'}}>도전을 마칠 때까지<br/> 플레이를 멈출 수 없습니다.</DialogTitle>
        <DialogActions>
          <Button onClick={onCloseClick} color="primary">
            취소
          </Button>
          <Button onClick={onStartClick} color="primary" autoFocus>
            확인
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default QuizStartConfirmDialog;