import { SETSCENE } from '../actions/scene'

const initialState = {
    _current_scene: 'IntroScene',//IndexScene
};

export default function scene(state = initialState, action) {
    console.log('reducer scene action call:' + action.type);
    switch(action.type) {
        case SETSCENE:
            console.log('SETSCENE:' + action._change_scene);
            return Object.assign({}, state, {
                _current_scene: action._change_scene
            });
        default:
            return state;
    }
}