import React, {useEffect} from 'react';
import queryString from 'query-string';
import { connect } from 'react-redux';
import { SetScene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';
import QuizResultScene from './QuizResultScene'

import IntroScene from './IntroScene'
import QuizSceneRoot from './QuizSceneRoot'


import { QuizData_OKR1 } from './data/QuizData_OKR1';
//import OKRIntroDesc from '../components/OKRIntroDesc'
import { QuizData_OKR2 } from './data/QuizData_OKR2';

import { QuizData_SHM } from './data/QuizData_SHM';
import SHMIntroDesc from '../components/SHMIntroDesc'

import { QuizData_TESAT } from './data/QuizData_TESAT';
import TESATIntroDesc from '../components/TESATIntroDesc'

import { QuizData_Economy1 } from './data/QuizData_Economy1';
import { QuizData_Economy2 } from './data/QuizData_Economy2';
import { QuizData_Philosophy } from './data/QuizData_Philosophy';
import { QuizData_History } from './data/QuizData_History';

import Iframe from 'react-iframe'

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  contents_site_link: {
    position: 'absolute',
    height: '100%',
    width: '100%',
    frameBorder: 0,
  },
}));

export const IndexScene = ({ _current_scene, SetScene, location }) => {
  console.log('Index_scene component start');

  // const _query = queryString.parse(location.search);
  // console.log(_query);
  const classes = useStyles();

  const [_subScene, setSubScene] = React.useState('');
  const [_rewardState, setRewardState] = React.useState(false);
  const [_rewardCategory, setRewardCategory] = React.useState('');
  const [_rewardExp, setRewardExp] = React.useState(0);
  const [_quizFullData, setQuizFullData] = React.useState(QuizData_OKR1);

  useEffect(() => {
    const query = queryString.parse(location.search);
    console.log(query);
    setSubScene(query.Scene);
    setRewardState((query.Complete == "true"));
    setRewardCategory(query.reward_category);
    setRewardExp(query.reward_exp);
  },[location]);


  useEffect(() => {
    selectQuizType();
  }, [_subScene]);

  const selectQuizType = () => {
    switch (_subScene) {
      case 'OKR1' : { setQuizFullData(QuizData_OKR1); } break;
      case 'OKR2' : { setQuizFullData(QuizData_OKR2); } break;
      case 'SHM' : { setQuizFullData(QuizData_SHM); } break;
      case 'TESAT' : { setQuizFullData(QuizData_TESAT); } break;
      case 'Economy1' : { setQuizFullData(QuizData_Economy1); } break;
      case 'Economy2' : { setQuizFullData(QuizData_Economy2); } break;
      case 'Philosophy' : { setQuizFullData(QuizData_Philosophy); } break;
      case 'History' : { setQuizFullData(QuizData_History); } break;
    }
  }

  const getIntroScene = () => {
    switch (_subScene) {
      case 'OKR1': return <Iframe url={process.env.PUBLIC_URL + '/contents/OKR1/index.html'} className={classes.contents_site_link} frameBorder="0"/>;
      case 'OKR2': return <Iframe url={process.env.PUBLIC_URL + '/contents/OKR2/index.html'} className={classes.contents_site_link} frameBorder="0"/>;
      case 'SHM': return <SHMIntroDesc />;
      case 'TESAT': return <Iframe url={process.env.PUBLIC_URL + '/contents/TESAT1/index.html'} className={classes.contents_site_link} frameBorder="0"/>;
      case 'Economy1': return <Iframe url={process.env.PUBLIC_URL + '/contents/Economy1/index.html'} className={classes.contents_site_link} frameBorder="0"/>;
      case 'Economy2': return <Iframe url={process.env.PUBLIC_URL + '/contents/Economy2/index.html'} className={classes.contents_site_link} frameBorder="0"/>;
      case 'Philosophy': return <Iframe url={process.env.PUBLIC_URL + '/contents/Philosophy/index.html'} className={classes.contents_site_link} frameBorder="0"/>;
      case 'History': return <Iframe url={process.env.PUBLIC_URL + '/contents/Economy1/index.html'} className={classes.contents_site_link} frameBorder="0"/>;
    } 
  }

  const getResultScene = () => {
    var iconPath = undefined;
    switch (_subScene) {
      case 'OKR1': iconPath = 'https://goli8111.github.io/level-up/img/OKRQuest_icon.png'; break;
      case 'OKR2': iconPath = 'https://goli8111.github.io/level-up/img/OKRQuest_icon.png'; break;
      case 'SHM': iconPath = 'https://goli8111.github.io/level-up/img/HomeTrainingQuest_icon.png'; break;
      case 'TESAT': iconPath = 'https://goli8111.github.io/level-up/img/THSATQuest_icon.png'; break;
      case 'Economy1': iconPath = 'https://goli8111.github.io/level-up/img/THSATQuest_icon.png'; break;
      case 'Economy2': iconPath = 'https://goli8111.github.io/level-up/img/THSATQuest_icon.png'; break;
      case 'Philosophy': iconPath = 'https://goli8111.github.io/level-up/img/THSATQuest_icon.png'; break;
      case 'History': iconPath = 'https://goli8111.github.io/level-up/img/THSATQuest_icon.png'; break;
    } 
    return (<QuizResultScene targetScore={_quizFullData.Config.goal_score} icon_path={iconPath} />);
  }

  const getScene = () => {
    //console.log('=====',_quizFullData);
    switch (_current_scene) {
      case 'IntroScene':
        return (<div> {getIntroScene()} <IntroScene rewardState={_rewardState} 
        rewardCategory={_rewardCategory} rewardExp={_rewardExp} /></div>);
      case 'QuizScene':
        return (<QuizSceneRoot quizData={_quizFullData} />);
      case 'ResultScene':
        return getResultScene();
    }
  }

  return (
    <div className={classes.root}>
      {getScene()}
    </div>
  );
}

const mapStateToProps = (state) => ({
  _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { SetScene };

export default connect(mapStateToProps, mapDispatchToProps)(IndexScene);