import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import './Quiz.css';

const useStyles = makeStyles((theme) => ({
    root: {
        position: "fixed",
        width: "100%",
        top: "8%",
        height: "92%",
        backgroundColor: '#000000CC',
    },
    text: {
        position: "fixed",
        width: '100%',
        height: '100%',
        textAlign: 'center',
        top: '-10%',
        fontSize: '400%',
        color: 'white',
        fontWeight: 'bold',
        textShadow: '2px 2px 5px black',
    }
}));

export const QuizMessageBox = ({ type, score }) => {
    console.log('QuizMessageBox call');
    const classes = useStyles();

    const getText = () => {
        switch (type) {
            case 'good':
                return (
                    <div className='quizMessageBox_box'>
                        +{score}
                        <br/>
                        <img className='quizMessageBox_right_icon' src={process.env.PUBLIC_URL + '/img/answer_icon.png'} />
                        <br/>
                        <span className='quizMessageBox_right_text'>GOOD</span>
                    </div>
                );
            case 'bad':
                return (
                    <div className='quizMessageBox_box'>
                        {score != 0 ? score : undefined}
                        <br />
                        <img className='quizMessageBox_right_icon' src={process.env.PUBLIC_URL + '/img/wrong_icon.png'} />
                        <br />
                        <span className='quizMessageBox_wrong_text'>OOPS</span>
                    </div>
                );
            default:
                return (<div className='quizMessageBox_box'>
                    <span>정답을<br/>입력해야 합니다.</span>
                </div>
                );
        }
    }

    // 카운팅 값이 없으면 렌더링 하지 않음
    return (
        <div className={classes.root}>
            <div className={classes.text}>
                {getText()}
            </div>
        </div>
    );
}

export default QuizMessageBox;
