import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import { SetScene } from '../actions/scene';
import { SetCheckAnswer, SetQuizStep } from '../actions/quiz';
import QuizScore from './../components/QuizScore'
import QuizSubmitButton from './../components/QuizSubmitButton'
import QuizBody from './../components/QuizBody';
import { SetIframeFullScreen } from './../components/util/SendMessage';

import './../components/Quiz.css';

const useStyles = makeStyles((theme) => ({
    root: {
        position: "absolute",
        width: "100%",
        height: "100%",
        backgroundColor: "black"
    },
    ScoreBar: {
        position: "fixed",
        top: "0px",
        margin: "1%",
        width: "98%",
        height: "8%",
        //backgroundColor: "green"
    },
    SubmitBar: {
        position: "fixed",
        bottom: "0px",
        margin: "1%",
        width: "98%",
        height: "6%",
        //backgroundColor: "green"
    },
    QuizBody: {
        position: "fixed",
        top: "8%",
        margin: "1%",
        width: "98%",
        height: "84.5%",
        //backgroundColor: "gray"
    },
}));

export const QuizScene = ({ quizConfig, quizData, SetScene, SetCheckAnswer, 
    SetQuizStep, _score, _step }) => {
    console.log('QuizScene component start');
    const classes = useStyles();
    const [_currentQuizData, setCurrentQuizData] = React.useState(undefined);
    //const [_quizStartTime, setQuizStartTime] = React.useState(0);
    const [_quizFinishTime, setQuizFinishTime] = React.useState(0);
  
    useEffect(() => {
        console.log('change step:'+_step);
        if (quizData != undefined) {
            var data = quizData[_step];
            console.log(data);
            setCurrentQuizData(data);
            var currentTime = new Date().getTime();
            setQuizFinishTime(currentTime + data.time_limit*1000);
            //setQuizStartTime(currentTime);
        }
    }, [quizData, _step]);

    useEffect(() => {
        SetIframeFullScreen(true);
        return ( () => {SetIframeFullScreen(false);}
        )
    }, []);
    
    const onAnswerSubmitClick = () => {
        console.log('onAnswerSubmitClick call');
        SetCheckAnswer(true);
    }

    return (
        <div className={classes.root}>
            <div className={classes.ScoreBar}>
                <QuizScore score={_score} target={quizConfig.goal_score} />
            </div>
            <div className={classes.SubmitBar} >
                <QuizSubmitButton finishTime={_quizFinishTime} onClick={onAnswerSubmitClick} /> 
            </div>
            <div className={classes.QuizBody} >
                <QuizBody step={_step} fullQuizData={quizData} quizData={_currentQuizData} finishTime={_quizFinishTime}/>
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    _score: state.quiz._score,
    _step: state.quiz._step,
});
const mapDispatchToProps = { SetScene, SetCheckAnswer, SetQuizStep };

export default connect(mapStateToProps, mapDispatchToProps)(QuizScene);