import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

import { connect } from 'react-redux';
import { SetScene } from '../actions/scene';

import { SetIframeFinish } from './../components/util/SendMessage';
import { playEnableClickSound, playQuestClearSound, playQuestFailSound } from './../components/util/SoundPlay'

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  BottomBarButton: {
    position: "fixed",
    height: "6%",
    top: "92%",
    background: 'linear-gradient(90deg, #cc6699 30%, #6666ff 80%)',
    borderRadius: 3,
    border: 0,
    color: 'white',
    boxShadow: '0 3px 5px 2px rgba(0, 0, 0, .3)',
    width: "94%",
    left: "3%",
    fontSize: "130%",
    fontWeight: "bold"
  }
}));

export const QuizResultScene = ({ SetScene, _score, targetScore, icon_path }) => {
  console.log('QuizResultScene component start');
  const classes = useStyles();

  const getResultText = () => {
    if (_score >= targetScore) {
      playQuestClearSound();
      return (
        <div className='quizResultScene_title_success'>
          클리어
        </div>
      );
    }

    playQuestFailSound();
    return (
      <div className='quizResultScene_title_fail'>
        실패
      </div>
    );
  }

  const getResultImage = () => {
    if (_score >= targetScore) {
      return (
        <img className='quizResultScene_background_img' src={process.env.PUBLIC_URL + '/img/success_background.png'} />
      );
    }
    return (
      <img className='quizResultScene_background_img' src={process.env.PUBLIC_URL + '/img/fail_background.png'} />
    );
  }

  const getResultButton = () => {
    if (_score >= targetScore) {
      return (
        <Button onClick={onFinishClick} className={classes.BottomBarButton} variant="contained" color="secondary">확인</Button>
      );
    }
    return (
      <Button onClick={onRetryClick} className={classes.BottomBarButton} variant="contained" color="secondary">확인</Button>
    );
  }

  const onFinishClick = () => {
    console.log('onFinishClick call');
    SetIframeFinish();
    //SetScene('IntroScene');
    playEnableClickSound();
  }

  const onRetryClick = () => {
    console.log('onRetryClick call');
    SetScene('IntroScene');
    playEnableClickSound();
  }

  return (
    <div className={classes.root}>
      <div className='quizResultScene_background'>
        {getResultImage()}
      </div>
      <div className='quizResultScene_score_box'>
        <div className='quizResultScene_score_left'>
          <span className='quizResultScene_score_title'>SCORE</span>
          <span className='quizResultScene_score_value'>{_score}</span>
        </div>
        <div className='quizResultScene_score_right'>
          <span className='quizResultScene_target_title'>TARGET</span>
          <span className='quizResultScene_target_value'>{targetScore}</span>
        </div>
      </div>
      <div className='quizResultScene_icon_box'>
        <img className='quizResultScene_icon' src={icon_path} />
      </div>
      <div className='quizResultScene_title_text'>
        {getResultText()}
      </div>
      <div className='quizResultScene_button_ok'>
        {getResultButton()}
      </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
  _score: state.quiz._score,
});
const mapDispatchToProps = { SetScene };

export default connect(mapStateToProps, mapDispatchToProps)(QuizResultScene);