import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import './Quiz.css';

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'absolute',
        width: "100%",
        height: "100%",
    },
}));

export const QuizScore = ({ score, target }) => {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <div className='quizScore_text'>
                <div className='quizScore_leftBox'>
                    <div className='quizScore_leftTitle'>SCORE</div>
                    <div className='quizScore_leftValue'>{score}</div>
                </div>

                <div className='quizScore_rightBox'>
                    <div className='quizScore_rightTitle'>TARGET</div>
                    <div className='quizScore_rightValue'>{target}</div>
                </div>
            </div>
        </div>
    );
}

export default QuizScore;
