import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';

import { connect } from 'react-redux';
import { SetScene } from '../actions/scene';
import { SetQuizReset } from '../actions/quiz';

import OKRIntroDesc from '../components/OKRIntroDesc'
import QuestStartButton from '../components/QuestStartButton'
import RewardButton from '../components/RewardButton'
import QuizStartConfirmDialog from '../components/QuizStartConfirmDialog'
import TextCountdown from '../components/TextCountdown'
import { GetIframeReward } from './../components/util/SendMessage';

import { playReadySound, playGoSound, playEnableClickSound, playDisableClickSound, playGetRewardSound } from './../components/util/SoundPlay'
const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
}));

export const IntroScene = ({ SetScene, SetQuizReset, rewardState, rewardCategory, rewardExp }) => {
  console.log('IntroScene component start');
  const classes = useStyles();
  const [openDialog, setOpenDialog] = React.useState(false);
  const [countdown, setcountdown] = React.useState(0);

  const onStartClick = () => {
    console.log('onStartClick call');
    setOpenDialog(true);
    playEnableClickSound();
  };

  useEffect(() => {
    SetQuizReset();
  }, []);


  const onStartCountdownClick = () => {
    console.log('onStartCountdownClick call');
    setOpenDialog(false);
    setcountdown(1);
    playReadySound();
    setTimeout(() => {
      setcountdown(2);
      playReadySound();
    }, 1000);
    setTimeout(() => {
      setcountdown(3);
      playReadySound();
    }, 2000);
    setTimeout(() => {
      SetScene('QuizScene');
      playGoSound();
    }, 3000);
  };

  const onStartConfirmCloseClick = () => {
    console.log('onStartConfirmCloseClick call');
    setOpenDialog(false);
    playEnableClickSound();
  };

  const onGetRewardClick = () => {
    console.log('onGetRewardClick call');
    GetIframeReward();
    playGetRewardSound();
  }

  const getMainButton = () => {
    //console.log('====rewardCategory:' + rewardCategory);
    if (true == rewardState) {
      return (<RewardButton onClick={onGetRewardClick} rewardCategory={rewardCategory} rewardExp={rewardExp} ></RewardButton>);
    } else {
      return (<QuestStartButton onClick={onStartClick} text='퀴즈 도전하기'></QuestStartButton>);
    }
  }

  return (
    <div className={classes.root}>
      {getMainButton()}
      
      <QuizStartConfirmDialog onStartClick={onStartCountdownClick} onCloseClick={onStartConfirmCloseClick} openDialog={openDialog} />
      <TextCountdown count={countdown} />
    </div>
  );
}

const mapStateToProps = (state) => ({
});
const mapDispatchToProps = { SetScene, SetQuizReset };

export default connect(mapStateToProps, mapDispatchToProps)(IntroScene);