import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%"
    },
    BottomBarBack: {
        position: "fixed",
        backgroundColor: '#FFFFFF88',
        width: '100%',
        height: '11%',
        top: '89%',
        left: '0%'
    },
    BottomBarButton: {
        position: "fixed",
        height: "6%",
        top: "92%",
        background: 'linear-gradient(90deg, #cc6699 30%, #6666ff 80%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        boxShadow: '0 3px 5px 2px rgba(0, 0, 0, .3)',
        width: "94%",
        left: "3%",
        fontSize: "130%",
        fontWeight: "bold"
    }
}));

  export const QuestStartButton = ({onClick, text}) => {
    const classes = useStyles();

    return (
        <div >
             <div className={classes.BottomBarBack}>
                <Button onClick={onClick} className={classes.BottomBarButton} variant="contained" color="secondary">{text}</Button>
            </div>
        </div>
    );
}

export default QuestStartButton;
